*{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  
}

body{
  padding:60 px;
}

input{
  color:grey;
  font-size: 1.2 rem;
  width: 100%;
}

.card{

  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-top: 10px;
  overflow: hidden;

}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
  padding: 0;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}